html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@font-face {
  font-family: "gBlack";
  src: url("../fonts/GothamBlack.woff2") format("woff2"), url("../fonts/GothamBlack.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "gBook";
  src: url("../fonts/GothamBook.woff2") format("woff2"), url("../fonts/GothamBook.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "gBold";
  src: url("../fonts/GothamBold.woff2") format("woff2"), url("../fonts/GothamBold.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

body {
  background: #FFF;
  line-height: 1.4;
  font-family: gBook,sans-serif;
}

.container {
  max-width: 1100px;
  margin: 0 auto;
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  padding-top: 45px;
}

.header__left {
  float: left;
}

.header__right {
  float: right;
  width: 60%;
  text-align: right;
  position: relative;
  top: 2px;
}

.nav {
  font-size: 14px;
  display: inline-block;
  vertical-align: middle;
  position: relative;
  top: 7px;
  margin-right: 20px;
}

.nav a {
  text-decoration: none;
  color: #b8bbc4;
  display: inline-block;
  position: relative;
  margin-left: 26px;
  overflow: hidden;
  padding-bottom: 4px;
}

.nav a:hover, .nav a.is-active {
  color: #fff;
}

.nav a:hover:before, .nav a.is-active:before {
  transform: translate(0, 0);
}

.nav a::before {
  position: absolute;
  left: 0;
  width: 100%;
  bottom: 0;
  height: 2px;
  background: #2addbc;
  content: "";
  transform: translate(-100%, 0);
  transition: transform .2s linear;
}

.explore {
  display: inline-block;
  height: 35px;
  line-height: 32px;
  font-size: 14px;
  color: #abaeb7;
  border: 1px solid #ABAEB7;
  border-radius: 10px;
  background: transparent;
  color: #dfe3ec;
  padding: 0 14px;
  vertical-align: middle;
}

.logo {
  color: #fff;
}

.logo__sp {
  border: 2px solid #F2F2F2;
  display: inline-block;
  vertical-align: middle;
  font-size: 19px;
  font-family: gBlack,sans-serif;
  color: #2addbd;
  height: 35px;
  line-height: 35px;
  width: 35px;
  text-align: center;
}

.logo__text {
  display: inline-block;
  vertical-align: middle;
  font-size: 19px;
  font-family: gBold,sans-serif;
}

.letter {
  display: inline-block;
  min-width: .3em;
}

.hero {
  background: #36373f url(../img/hero.jpg) no-repeat 50% 50%;
  background-size: cover;
  height: 100vh;
  padding-top: 300px;
  color: #fff;
  line-height: 1.5;
  text-align: center;
  font-size: 16px;
}

.hero__title {
  font-size: 80px;
  color: #2addbc;
  font-family: gBlack,sans-serif;
  line-height: 1;
  margin-bottom: 30px;
}

.hero__action {
  text-align: center;
  margin-top: 120px;
}

.hero__start {
  border-bottom: 2px solid #2addbc;
  padding: 0 16px 10px 16px;
  text-transform: uppercase;
  color: #fff;
  text-decoration: none;
}

.loader {
  background: #000;
  position: fixed;
  height: 100%;
  width: 100%;
  z-index: 2000;
  mix-blend-mode: multiply;
}

.loader__inside {
  width: 140vw;
  height: 140vw;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%) scale(0.01);
  background: #fff;
  border-radius: 50%;
}
